header.obrigado-header {
    height: 100px;
    overflow: hidden;
}

.container-obrigado {
    height: calc(100vh - 180px);
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    h1 {
	    font-size: 50px;
	    font-weight: bold;
	    margin-bottom: 10px;
    }
}

footer.obrigado-footer {
    background-color: #c1b2d0;
    height: 80px;
    position: relative;
}